.login-prime-container{
    width: 100%;
    background-color: #fff;
    padding: 20px;
    box-sizing: border-box;

    .loginprime-menu {
        width: 63%;
        margin-bottom: 20px;
        background-color: #F0F0F0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 13px 13px 13px 13px;
        li{
            list-style: none;
            text-align: center;
            width: 100%;
            background: #fff;
            margin: 0px 7px;
            display: block;
            a{
                color: #000;
                text-decoration: none;
                padding: 16px 35px;
                display: inline-block;
                font-size: 20px;
                width: 60%;
                &.active{
                    background-color: #34495E;
                    color: #fff;
                }
                &:hover{
                    background-color: #34495E;
                    color: #fff;
                }
            }
            
        }
    }
    .lp-settings-notification{
        width: 63%;
        padding: 10px;
        margin-bottom: 10px;
    }
    .lp-settings-section{
        // width: 61%;
        padding: 30px;
        border-radius: 12px;
        margin-bottom: 40px;
        box-shadow: 2px 2px 7px 0 rgba(0, 0, 0, 0.25);
        h2{
            color: #2B8FBB;
        }
        .form-table{
            td {
                text-align: right;
                width: 70%;
                select{
                    max-width: inherit;
                    border-radius: 8px;
                    padding: 10px;
                }
                input{
                    border-radius: 8px;
                    padding: 10px;
                }
                input[type="color"]{
                    border-radius: 0;
                    padding: 0;
                }
            }
        }
    }
    .lp-settings-submit{
        width: 65%;
        .form-table{
            td {
                text-align: right;
                .button-secondary{
                    margin-right: 20px;
                }
            }
        }
    }
}

.components-form-file-upload {
    margin-bottom: 10px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
}
button.closebtn {
    position: absolute;
